import { ResponsiveBubble } from '@nivo/circle-packing'

const bubble = (name, loc) => (
  <ResponsiveBubble
      root={{
        "name": name,
        "loc": loc,
        "color": "#fce064",
    }}
      margin={{
          "top": 20,
          "right": 20,
          "bottom": 20,
          "left": 20
      }}
      identity="name"
      value="loc"
      colorBy={node => node.color}
      enableLabel={true}
      isInteractive={true}
      isZoomable={false}
      padding={15}
      leavesOnly={true}
      label={node => `${node.id}: ${node.value}`}
      labelTextColor="inherit:darker(1)"
      borderWidth={2}
      animate={true}
      motionStiffness={90}
      motionDamping={12}
  />
)

const horizontalBubbles = () => (
  <div style={{height: "300px"}}>
    <bubble name="Biologie, médecine et santé" loc={423} />
    <bubble name="Sciences de la société" loc={119} />
    <bubble name="Sciences humaines" loc={43} />
    <bubble name="Sciences de l'ingénieur" loc={38} />);
  </div>);


export default horizontalBubbles;
